body{
    margin: 0px 0px ; 
    font-size: x-large;
    background-color: #19083b; 
    color:white ;
}

a {
    text-decoration: none ;
    /* color: grey;   */
    color: white;  
}

.li {
    color : grey ;
}


.hover_button:hover{
    background-color: rgb(0, 235, 252);
    /* width: 500px;
    border-radius: 50%; */
  } 

nav { 
    padding: 30px 0px ;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    background-color:#150734;
    font-family: cursive; 
    position: sticky;
    /* color: white; */
    top: 0px;
}



form{
    display: flex;
    flex-direction: column;  
    align-items: center; 
    padding-top: 5%;
    background-color: #19083b;
    height: 1000px; 
     
}
 
form p {
    padding: 10px 10px ; 
}
 
form button{
    background-color: #2c0e6d;
    /* color: blueviolet;  */
    color: white;
    font-family: cursive;
    width: 75px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid rgb(0, 221, 255);
    /* margin: 10px 0px ; */
    cursor: pointer;
}

form div{
    display: flex;
    flex-direction: column;
    align-items: center; 
    border-radius: 10px;
    background-color: #19083b;
    border: 3px solid rgb(0, 221, 255);
    padding: 20px 0px ; 
} 


.fox{ 
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    height: 1000px;
    background-color: #19083b;
}
 




table,tr,th ,td {
    font-size: xx-large;
    text-align: center;
    padding: 10px 35px ;
    border : 2px solid rgb(0, 221, 255);
    border-collapse : collapse ;
    box-sizing: border-box;
    margin: 10px 20px;
    
}
th {
    font-weight: bolder;
    font-size: larger; 
    font-style: italic;
}
 

 



